:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-start: hsl(210, 78%, 56%);
  --toggle-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --veryDrkBlue-bg: hsl(230, 17%, 14%);
  --veryDarkBlue-top-bg: hsl(232, 19%, 15%);
  --drk-des-blue-cardBG: hsl(228, 28%, 20%);
  --hover-cardBG: hsla(228, 23%, 42%, 0.5);
  --des-blue-text: hsl(228, 34%, 66%);
  --white: hsl(0, 0%, 100%);
  --pale-blue-topBG: hsl(225, 100%, 98%);
  --card-bg: hsl(227, 47%, 96%);
  --dark-blue-text1: hsl(228, 12%, 44%);
  --drk-blue-text2: hsl(230, 17%, 14%);
}

:root {
  --interFont: "Inter", "sans-serif";
}

@media (min-width: 71.875em) {
  h1 {
    font-size: 3em;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--interFont);
  font-size: 1.25em;
  background-color: var(--veryDrkBlue-bg);
  color: var(--white);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.3;
}

h1 {
  font-size: 1.125em;
  margin-bottom: 0.1875em;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75em;
  }
}

a:visited,
a:active {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  padding: 0 1.5625em;
  max-width: 69.375em;
  margin: 0 auto 2.875em;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25em;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__followers {
  font-size: 0.5625em;
  font-weight: 700;
  color: var(--des-blue-text);
}
.toggle {
  border: none;
}
.toggle__wrapper {
  display: inline-block;
  height: 1.5em;
  position: relative;
  width: 70px;
  text-align: center;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
}
.toggle input[type=radio][type=system] {
  margin: 0 5px;
}
.toggle__background {
  display: block;
  height: 100%;
  border-radius: 0.75em;
  margin-top: -25px;
  background: linear-gradient(to right, var(--toggle-start), var(--toggle-end));
}
.toggle__button {
  position: absolute;
  top: 0.1875em;
  left: 0.1875em;
  width: 1.125em;
  height: 1.125em;
  border-radius: 50%;
  background-color: var(--drk-des-blue-cardBG);
  transition: all 0.2s ease-in-out;
}
#light:checked ~ .toggle__button {
  left: calc(100% - 28px);
  right: 0;
}
#system:checked ~ .toggle__button {
  left: 33%;
}
#light:focus ~ .toggle__button {
  background: var(--white);
}
#dark:focus ~ .toggle__background {
  background: var(--dark-blue-text1);
}
#dark:focus ~ .toggle__button {
  background: var(--toggle);
}
.toggle label {
  font-size: 0.7rem;
  color: var(--toggle);
}
.toggle label[for=dark] {
  margin-right: 0.3125em;
}
.toggle label[for=light] {
  margin-left: 0.3125em;
}
.toggle label[for=system] {
  position: absolute;
  top: 3.125em;
  left: 1.5625em;
}
@media (max-width: 39.9375em) {
  .toggle {
    text-align: left;
    border-top: 1px solid var(--dark-blue-text1);
    margin-top: 20px;
    padding-top: 20px;
  }
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: ce;
  gap: 1.875em;
}
@media (min-width: 40em) {
  .cards {
    justify-content: space-between;
  }
}

.card {
  background: var(--drk-des-blue-cardBG);
  width: 13.875em;
  padding: 1.25em;
  text-align: center;
  border-radius: 0.3125em;
}
.card:hover {
  cursor: pointer;
  background: var(--hover-cardBG);
}
.card__profile__flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__username {
  font-size: 0.625em;
  margin-left: 0.5em;
  color: var(--des-blue-text);
}
.card__followers {
  margin: 0.625em 0;
}
.card__followers--number {
  display: block;
  font-size: 2.5em;
  font-weight: 700;
  margin-bottom: -0.1875em;
}
.card__followers--text {
  text-transform: uppercase;
  letter-spacing: 0.1875em;
  font-size: 0.625em;
  color: var(--dark-blue-text1);
  font-weight: 700;
}
.card__count--green {
  color: var(--lime-green);
}
.card__count--red {
  color: var(--bright-red);
}
.card__facebook {
  border-top: 3px solid var(--facebook);
}
.card__twitter {
  border-top: 3px solid var(--twitter);
}
.card__instagram {
  position: relative;
  overflow: hidden;
}
.card__instagram::before {
  content: "";
  height: 0.1875em;
  width: 100%;
  background: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
  position: absolute;
  top: 0;
  left: 0;
}
.card__youtube {
  border-top: 3px solid var(--youtube);
}
.card-overview, .card-count {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-overview {
  margin-bottom: 1.5625em;
}
.card-status {
  font-size: 0.625em;
  font-weight: 700;
  color: var(--des-blue-text);
}
.card-num {
  font-size: 1.875em;
}
.card-percentage {
  font-size: 0.625em;
  margin-left: 0.3125em;
}